<template>
  <div class="table-section">
    <h3 class="section-title">{{ title }} 数据资源入表详情</h3>
    <div class="table-wrapper">
      <table class="data-table">
        <thead>
          <tr>
            <th>报告时间</th>
            <th>{{ title }}-数据资源入表数量</th>
            <th>{{ title }}-数据资源入表总额（万元）</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="row in formattedRows" :key="row.reportTime">
            <td>{{ row.reportTime }}</td>
            <td style="text-align: right;">{{ formatValue(row.count) }}</td>
            <td style="text-align: right;">{{ formatNumber(row.total) }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SubjectTable',
  props: {
    title: String,
    countData: Array,
    sumData: Array
  },
  computed: {
    formattedRows() {
      const rowMap = {};
      const countKey = `${this.title}-数据资源入表数量`;
      const totalKey = `${this.title}-数据资源入表总额（万元）`;

      // 遍历数量数据
      this.countData.forEach(item => {
        const time = item['报告时间'];
        if (!time || time === '合计') return; // ✅ 忽略空时间或已有合计行
        if (!rowMap[time]) {
          rowMap[time] = { reportTime: time, count: 0, total: 0 };
        }
        rowMap[time].count = parseInt(item[countKey], 10) || 0;
      });

      // 遍历金额数据
      this.sumData.forEach(item => {
        const time = item['报告时间'];
        if (!time || time === '合计') return; // ✅ 忽略空时间或已有合计行
        if (!rowMap[time]) {
          rowMap[time] = { reportTime: time, count: 0, total: 0 };
        }
        rowMap[time].total = parseFloat(item[totalKey]) || 0;
      });

      const rows = Object.values(rowMap).sort((a, b) => a.reportTime.localeCompare(b.reportTime));

      // ✅ 添加合计行，仅添加一次，排除重复
      const totalRow = {
        reportTime: '合计',
        count: rows.reduce((sum, r) => sum + (parseInt(r.count) || 0), 0),
        total: rows.reduce((sum, r) => sum + (parseFloat(r.total) || 0), 0)
      };

      return [...rows, totalRow];
    }
  },
  methods: {
    formatNumber(val) {
      const num = parseFloat(val);
      return isNaN(num) ? '—' : num.toLocaleString(undefined, { minimumFractionDigits: 2 });
    },
    formatValue(val) {
      return (val === null || val === undefined || isNaN(val)) ? '—' : val;
    }
  }
};
</script>

<style scoped>
.table-section {
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
}
.section-title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 16px;
  color: #003049;
}
.table-wrapper {
  overflow-x: auto;
}
.data-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 14px;
}
.data-table th,
.data-table td {
  border: 1px solid #ccc;
  padding: 10px 12px;
  text-align: center;
}
.data-table th {
  background-color: #f7f7f7;
  color: #003049;
  font-weight: bold;
}
</style>
