<template>
  <div class="chart-wrapper" style="position: relative;">
    <div class="chart-title">{{ chartTitle }}</div>
    <ChartSpinner :visible="loading" />
    <v-chart
      v-if="!loading && chartOption.series && chartOption.series.length"
      :option="chartOption"
      style="width: 100%; height: 440px;"
    />
  </div>
</template>

<script>
import { defineComponent, ref, watch } from 'vue';
import VChart from 'vue-echarts';
import ChartSpinner from '@/components/common/ChartSpinner.vue';
import chartColors from '@/utils/chartColors.js';

export default defineComponent({
  name: 'ComboBarLineChart',
  components: {
    VChart,
    ChartSpinner
  },
  props: {
    chartTitle: {
      type: String,
      default: '柱状图 + 折线图'
    },
    categories: {
      type: Array,
      default: () => []  // ✅ 防止未传时报错
    },
    barSeries: {
      type: Array,
      default: () => []  // ✅ 防止未传时报错
    },
    lineSeries: {
      type: Object,
      default: () => ({ name: '折线', data: [] }) // ✅ 保底结构
    },
    yAxisBarName: {
      type: String,
      default: '数量（个）'
    },
    yAxisLineName: {
      type: String,
      default: '比例（%）'
    }
  },
  setup(props) {
    const chartOption = ref({});
    const loading = ref(false);

    const updateChart = () => {
      loading.value = true;
      setTimeout(() => {
        const bar = (props.barSeries || []).map((s, idx) => ({
          name: s.name,
          type: 'bar',
          data: s.data || [],
          yAxisIndex: 0,
          itemStyle: {
            color: chartColors[idx % chartColors.length]
          }
        }));

        const line = {
          name: props.lineSeries?.name || '折线',
          type: 'line',
          data: props.lineSeries?.data || [],
          yAxisIndex: 1,
          symbol: 'circle',
          lineStyle: { width: 3 },
          itemStyle: {
            color: chartColors[props.barSeries.length % chartColors.length]
          }
        };

        chartOption.value = {
          color: chartColors,
          tooltip: {
            trigger: 'axis',
            axisPointer: { type: 'shadow' }
          },
          legend: { top: 10 },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: {
            type: 'category',
            data: props.categories || [],
            axisLabel: {
              fontSize: 12,
              rotate: 0,
              formatter: val => (val.length > 6 ? val.slice(0, 6) + '…' : val)
            }
          },
          yAxis: [
            {
              type: 'value',
              name: props.yAxisBarName,
              nameLocation: 'middle',
              nameGap: 45,
              nameTextStyle: { fontSize: 14 }
            },
            {
              type: 'value',
              name: props.yAxisLineName,
              nameLocation: 'middle',
              nameGap: 45,
              nameTextStyle: { fontSize: 14 }
            }
          ],
          series: [...bar, line]
        };

        loading.value = false;
      }, 500);
    };

    watch(
      () => [props.barSeries, props.lineSeries, props.categories],
      updateChart,
      { immediate: true, deep: true }
    );

    return {
      chartOption,
      loading
    };
  }
});
</script>

<style scoped>
.chart-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  min-width: 1000px;
}

.chart-title {
  font-size: 18px;
  font-weight: bold;
  margin: 20px 0;
  text-align: center;
  color: #003049;
}
</style>
