<template>
  <div class="dashboard-page">
    <LoadingSpinner :visible="isLoading" />

    <div class="dashboard-title-block" v-show="!isLoading">
      <h1 class="dashboard-title">上市公司数据资产情况</h1>
      <p class="dashboard-subtitle">Overview of Listed Company Data Assets</p>
    </div>

    <LFilterSection
      v-show="!isLoading"
      :filters="filters"
      @update:filters="onFilterUpdate"
      mode="ls"
    />

    <div class="dashboard-container" v-show="!isLoading">
      <LChartRow :filters="filters" />
    </div>
  </div>
</template>

<script>
import LFilterSection from '@/components/dashboard/filters/LFilterSection.vue';
import LChartRow from '@/components/dashboard/LChartRow.vue';
import LoadingSpinner from '@/components/common/LoadingSpinner.vue';

export default {
  name: 'LSDashboardPage',
  components: {
    LFilterSection,
    LChartRow,
    LoadingSpinner
  },
  data() {
    return {
      isLoading: true,
      filters: {
        province_area: '',
        quarter: ''
      }
    };
  },
  methods: {
    onFilterUpdate(newFilters) {
      this.filters = { ...this.filters, ...newFilters };
      this.isLoading = false;
    }
  },
  mounted() {
    // 初始加载完立即关闭 loading
    setTimeout(() => { this.isLoading = false; }, 600);
  }
};
</script>

<style scoped>
.dashboard-page {
  display: flex;
  flex-direction: column;
  background: #f5f7fa;
  min-height: 100vh;
}

.dashboard-title-block {
  margin: 30px 30px 10px;
}

.dashboard-title {
  font-size: 24px;
  font-weight: bold;
  color: #003049;
  margin: 0;
}

.dashboard-subtitle {
  font-size: 18px;
  font-weight: bold;
  color: #005f73;
  margin-top: 6px;
}

.dashboard-container {
  padding: 30px;
  background: #f9f9f9;
}
</style>