<template>
  <div class="chart-grid">
    <!-- 第1行：Combo 柱状图 + 折线图 + 表格 -->
    <div class="chart-row">
      <div class="chart-full" v-if="comboData.barSeries?.length && comboData.lineSeries?.length">
        <ComboBarLineChart 
          chartTitle="入表趋势图（数量 + 金额）"
          :categories="comboData.categories"
          :barSeries="comboData.barSeries"
          :lineSeries="comboData.lineSeries"
        />
      </div>
    </div>

    <div class="chart-row">
      <div class="chart-full" v-if="comboData.tableRows?.length">
        <ComboTable :table-rows="comboData.tableRows" />
      </div>
    </div>

    <!-- 第2行：Grouped 柱状图（数量+金额） -->
    <div class="chart-row">
      <div class="chart-half" v-if="groupedSubjectData.countSeries?.length">
        <GroupedBarChart
          :chart-data="groupedSubjectData.countSeries"
          title="按入表科目统计数量"
          row-key="入表科目"
          col-key="报告时间"
        />
      </div>
      <div class="chart-half" v-if="groupedSubjectData.sumSeries?.length">
        <GroupedBarChart
          :chart-data="groupedSubjectData.sumSeries"
          title="按入表科目统计金额"
          row-key="入表科目"
          col-key="报告时间"
        />
      </div>
    </div>

    <!-- 第3行：三张表格 -->
    <div class="chart-row">
      <div
        class="chart-full"
        v-for="(subject, index) in filteredSubjectNames"
        :key="index"
      >
        <SubjectTable
          :title="subject"
          :count-data="subjectTables[subject]?.count"
          :sum-data="subjectTables[subject]?.total"
        />
      </div>
    </div>

    <!-- 第4~7行：Grouped 图 + 表格（行业、规模、省份、实控人） -->
    <div class="chart-row" v-for="item in groupedFields" :key="item.field">
      <div class="chart-full" v-if="groupedCharts[item.field]?.length">
        <GroupedBarChart
          :chart-data="groupedCharts[item.field]"
          :title="item.title"
          row-key="name"
          col-key="报告时间"
        />
      </div>
      <div class="chart-full" v-if="groupedCharts[item.field]?.length">
        <TransposedTable
          :data="groupedCharts[item.field]"
          :row-key="'name'"
          col-key="报告时间"
          val-key="value"
        />
      </div>
    </div>

    <!-- 最后一行：两个实控人归类表格 -->
    <div class="chart-row">
      <div class="chart-half" v-if="controlData.original?.length">
        <TransposedTable
          :data="controlData.original"
          row-key="实控人"
          col-key="报告时间"
          val-key="value"
          title="实控人原始分布"
        />
      </div>
      <div class="chart-half" v-if="controlData.classified?.length">
        <TransposedTable
          :data="flattenClassified(controlData.classified)"
          row-key="name"
          col-key="报告时间"
          val-key="value"
          title="实控人分类汇总"
        />
      </div>
    </div>
  </div>
</template>

<script>
import ComboBarLineChart from '@/components/dashboard/charts/ComboBarLineChart.vue';
import ComboTable from '@/components/dashboard/charts/ComboTable.vue';
import GroupedBarChart from '@/components/dashboard/charts/GroupedBarChart.vue';
import SubjectTable from '@/components/dashboard/charts/SubjectTable.vue';
import TransposedTable from '@/components/dashboard/charts/TransposedTable.vue';
import axios from 'axios';

export default {
  name: 'LChartRow',
  components: {
    ComboBarLineChart,
    ComboTable,
    GroupedBarChart,
    SubjectTable,
    TransposedTable
  },
  props: {
    filters: { type: Object, required: true }
  },
  computed: {
    filteredSubjectNames() {
      console.log('📌 subjectTables:', this.subjectTables);
      return this.subjectNames.filter(name => {
        const entry = this.subjectTables[name];
        return Array.isArray(entry?.count) && entry.count.length > 0;
      });
    }
  },
  data() {
    return {
      comboData: { barSeries: [], lineSeries: [], tableRows: [], tableColumns: [] },
      groupedSubjectData: { countSeries: [], sumSeries: [] },
      subjectTables: {},
      groupedCharts: {},
      controlData: { original: [], classified: [] },
      subjectNames: ['无形资产', '开发支出', '存货'],
      groupedFields: [
        { field: '所属证券行业分布', displayKey: '所属证券行业分布', title: '所属证券行业分布' },
        { field: '市值规模', displayKey: '市值规模', title: '市值规模分布' },
        { field: '省份', displayKey: '省份', title: '省份分布' },
        { field: '实控人', displayKey: '实控人', title: '实控人分布' }
      ]
    };
  },
  watch: {
    filters: {
      handler() { this.loadAllData(); },
      deep: true,
      immediate: true
    }
  },
  methods: {
    formatToGroupedSeries(raw, quarters = ['Q1', 'Q2', 'Q3', 'Q4']) {
      const grouped = {};
      raw.forEach(({ name, '报告时间': quarter, value }) => {
        if (!grouped[name]) grouped[name] = Array(quarters.length).fill(0);
        const idx = quarters.indexOf(quarter);
        if (idx !== -1) grouped[name][idx] = parseFloat(value);
      });

      return Object.entries(grouped).map(([name, data]) => ({ name, data }));
    },

    flattenClassified(data) {
      const result = [];
      data.forEach(row => {
        const { name, definition, ...rest } = row;
        Object.keys(rest).forEach(key => {
          result.push({
            name,
            definition,
            '报告时间': key,
            value: rest[key]
          });
        });
      });
      return result;
    },
    async loadAllData() {
      console.log('✅ subjectTables keys:', Object.keys(this.subjectTables));
      console.log('✅ groupedSubjectData.countSeries:', this.groupedSubjectData.countSeries);
      console.log('✅ groupedSubjectData.sumSeries:', this.groupedSubjectData.sumSeries);

      try {
        const [comboRes, barRes, tableRes, ...groupedRes] = await Promise.all([
          axios.post('/api/lchart/combo1', { filters: this.filters }),
          axios.post('/api/lchart/subject-bars', { filters: this.filters }),
          axios.post('/api/lchart/subject-tables', { filters: this.filters }),
          ...this.groupedFields.map(f => axios.post('/api/lchart/group-field', {
            filters: this.filters,
            field: f.field
          }))
        ]);

        const barRaw = comboRes.data.barSeries || [];
        const lineRaw = comboRes.data.lineSeries || [];

        this.comboData.barSeries = [
          {
            name: '数据资源入表数量',
            data: barRaw.map(item => item['数据资源入表数量'])
          }
        ];

        this.comboData.lineSeries = {
          name: '数据资源入表总额',
          data: lineRaw.map(item => item['数据资源入表总额（单位：万元）'])
        };

        this.comboData.categories = barRaw.map(item => item['报告时间']);
        // 先建立一个报告时间 → 总额的映射
        const lineMap = {};
        lineRaw.forEach(item => {
          lineMap[item['报告时间']] = item['数据资源入表总额（单位：万元）'];
        });

        // 构建表格数据
        this.comboData.tableRows = barRaw.map(row => ({
          '报告时间': row['报告时间'],
          '入表数量': row['数据资源入表数量'],
          '入表总额（万元）': lineMap[row['报告时间']] || 0
        }));
        this.comboData.tableColumns = ['报告时间', '入表数量', '入表总额（万元）'];

        this.groupedSubjectData = barRes.data;
        this.subjectTables = tableRes.data;

        this.groupedFields.forEach((f, idx) => {
          const rawData = groupedRes[idx].data;
          // ✅ 格式化 value 字段，确保是数字
          const cleanData = rawData.map(row => ({
            ...row,
            value: parseFloat(row.value)
          }));
          this.groupedCharts[f.field] = cleanData;
        });

        const controlRes = await axios.post('/api/lchart/control-summary', { filters: this.filters });
        this.controlData = controlRes.data;

      } catch (err) {
        console.error('❌ 加载图表数据失败', err);
      }
    }
  }
};
</script>

<style scoped>
.chart-grid {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.chart-row {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.chart-full {
  flex: 1 1 100%;
  background: #fff;
  padding: 20px;
  border-radius: 8px;
}

.chart-half {
  flex: 1 1 48%;
  background: #fff;
  padding: 20px;
  border-radius: 8px;
}
</style>
