<template>
  <div class="chart-wrapper" style="position: relative;">
    <div class="chart-title">{{ chartTitle }}</div>
    <ChartSpinner :visible="loading" />
    <v-chart :option="chartOption" style="width: 100%; height: 440px;" />
  </div>
</template>

<script>
import { defineComponent, ref, watch } from 'vue';
import VChart from 'vue-echarts';
import ChartSpinner from '@/components/common/ChartSpinner.vue';
import chartColors from '@/utils/chartColors.js';

export default defineComponent({
  name: 'GroupedBarChart',
  components: {
    VChart,
    ChartSpinner
  },
  props: {
    chartTitle: {
      type: String,
      default: 'Grouped Bar Chart'
    },
    chartData: {
      type: Array,
      default: () => [] // ✅ 加默认值，避免 undefined
    },
    rowKey: {
      type: String,
      default: '类别'
    },
    valKey: {
      type: String,
      default: 'value'
    }
  },
  setup(props) {
    const chartOption = ref({});
    const loading = ref(false);

    const updateChart = () => {
      loading.value = true;

      setTimeout(() => {
        // ✅ 加默认值防御
        const data = props.chartData || [];

        // 1. 提取所有唯一类目
        const categoriesSet = new Set();
        data.forEach(series => {
          (series.data || []).forEach(item => {
            categoriesSet.add(item[props.rowKey]);
          });
        });
        const categories = Array.from(categoriesSet);

        // 2. 构建每个 series 的 data（对齐 categories）
        const seriesData = data.map((series, idx) => {
          const dataMap = Object.fromEntries(
            (series.data || []).map(item => [item[props.rowKey], item[props.valKey]])
          );
          const alignedData = categories.map(c => dataMap[c] || 0);
          return {
            name: series.name,
            type: 'bar',
            data: alignedData,
            barGap: 0,
            barCategoryGap: '50%',
            itemStyle: {
              color: chartColors[idx % chartColors.length]
            }
          };
        });

        chartOption.value = {
          color: chartColors,
          tooltip: {
            trigger: 'axis',
            axisPointer: { type: 'shadow' }
          },
          legend: { top: 10 },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: {
            type: 'category',
            data: categories,
            axisLabel: {
              fontSize: 12,
              rotate: 0,
              formatter: val => (val.length > 6 ? val.slice(0, 6) + '…' : val)
            }
          },
          yAxis: {
            type: 'value',
            name: '数量（个）',
            nameLocation: 'middle',
            nameGap: 50,
            nameTextStyle: { fontSize: 14 }
          },
          series: seriesData
        };

        loading.value = false;
      }, 300);
    };

    watch(() => props.chartData, updateChart, { immediate: true, deep: true });

    return {
      chartOption,
      loading
    };
  }
});
</script>

<style scoped>
.chart-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  min-width: 1000px;
}

.chart-title {
  font-size: 18px;
  font-weight: bold;
  margin: 20px 0;
  text-align: center;
  color: #003049;
}
</style>
