<template>
  <div class="filter-section">
    <!-- 第一行：报告时间、地区 -->
    <div class="filter-row">
      <div class="filter-item">
        <label>报告时间：</label>
        <select v-model="quarter">
          <option value="">全部</option>
          <option v-for="option in options.quarter" :key="option" :value="option">{{ option }}</option>
        </select>
      </div>
      <div class="filter-item">
        <label>省份：</label>
        <select v-model="province_area">
          <option value="">全部</option>
          <option v-for="option in options.province_area" :key="option" :value="option">{{ option }}</option>
        </select>
      </div>
    </div>

    <!-- 第二行：模糊搜索 公司、所属证券行业分布 -->
    <div class="filter-row">
      <div class="filter-item">
        <label>公司：</label>
        <el-select
          v-model="company"
          filterable
          remote
          clearable
          placeholder="请输入公司关键词"
          :remote-method="fetchCompanyOptions"
          :loading="loading.company"
          @clear="onCompanyCleared"
        >
          <el-option v-for="item in companyOptions" :key="item.value" :label="item.label" :value="item.value" />
        </el-select>
      </div>
      <div class="filter-item">
        <label>所属证券行业分布：</label>
        <el-select
          v-model="dataasset_content"
          filterable
          remote
          clearable
          placeholder="请输入行业关键词"
          :remote-method="fetchContentOptions"
          :loading="loading.content"
          @clear="onContentCleared"
        >
          <el-option v-for="item in contentOptions" :key="item.value" :label="item.label" :value="item.value" />
        </el-select>
      </div>
    </div>

    <!-- 第三行：按钮 -->
    <div class="filter-row row-actions">
      <div class="action-buttons">
        <button class="confirm-btn" @click="confirmFilters">确认</button>
        <button class="clear-all-btn" @click="clearFilters">清除筛选</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'LFilterSection',
  props: {
    filters: { type: Object, required: true },
    options: { type: Object, required: true }
  },
  data() {
    return {
      quarter: '',
      province_area: '',
      company: '',
      dataasset_content: '',
      companyOptions: [],
      contentOptions: [],
      loading: {
        company: false,
        content: false
      }
    };
  },
  watch: {
    filters: {
      immediate: true,
      deep: true,
      handler(newVal) {
        this.quarter = newVal.quarter || '';
        this.province_area = newVal.province_area || '';
        this.company = newVal.company || '';
        this.dataasset_content = newVal.dataasset_content || '';
      }
    }
  },
  methods: {
    fetchCompanyOptions(query) {
      if (!query) return;
      this.loading.company = true;
      axios
        .get('/api/lasset/search/company?q=' + query)
        .then(res => {
          this.companyOptions = res.data.map(item => ({ label: item, value: item }));
        })
        .finally(() => {
          this.loading.company = false;
        });
    },
    fetchContentOptions(query) {
      if (!query) return;
      this.loading.content = true;
      axios
        .get('/api/lasset/search/content?q=' + query)
        .then(res => {
          this.contentOptions = res.data.map(item => ({ label: item, value: item }));
        })
        .finally(() => {
          this.loading.content = false;
        });
    },
    confirmFilters() {
      this.$emit('update:filters', {
        quarter: this.quarter,
        province_area: this.province_area,
        company: this.company,
        dataasset_content: this.dataasset_content
      });
    },
    clearFilters() {
      this.quarter = '';
      this.province_area = '';
      this.company = '';
      this.dataasset_content = '';
      this.confirmFilters();
    },
    onCompanyCleared() {
      this.company = '';
      this.confirmFilters();
    },
    onContentCleared() {
      this.dataasset_content = '';
      this.confirmFilters();
    }
  }
};
</script>

<style scoped>
.filter-section {
  background-color: white;
  padding: 20px 30px;
  border-radius: 8px;
  margin: 30px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.filter-row {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}
.filter-item {
  display: flex;
  align-items: center;
  gap: 10px;
  flex: 1 1 300px;
}
.filter-item label {
  font-weight: bold;
  color: #003049;
}
select, input {
  padding: 6px 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
  min-width: 160px;
}
.row-actions {
  justify-content: flex-end;
}
.action-buttons {
  display: flex;
  gap: 30px;
  margin-right: 100px;
}
.confirm-btn,
.clear-all-btn {
  background-color: #003049;
  color: white;
  padding: 6px 22px;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 20px;
  min-width: 100px;
}
.confirm-btn:hover,
.clear-all-btn:hover {
  background-color: white;
  color: #003049;
}
</style>
