<template>
  <div class="table-section">
    <h3 class="section-title">入表趋势数据详情</h3>
    <div class="table-wrapper">
      <table class="data-table">
        <thead>
          <tr>
            <th>报告时间</th>
            <th>数据资源入表数量</th>
            <th>数据资源入表总额（单位：万元）</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="row in tableRows" :key="row.报告时间">
            <td>{{ row['报告时间'] }}</td>
            <td style="text-align: right;">{{ row['入表数量'] }}</td>
            <td style="text-align: right;">{{ format(row['入表总额（万元）']) }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ComboTable',
  props: {
    tableRows: Array
  },
  methods: {
    format(val) {
      const num = parseFloat(val);
      return isNaN(num) ? '—' : num.toLocaleString(undefined, { minimumFractionDigits: 2 });
    }
  }
};
</script>

<style scoped>
.table-section {
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
}
.section-title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 16px;
  color: #003049;
}
.table-wrapper {
  overflow-x: auto;
}
.data-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 14px;
}
.data-table th,
.data-table td {
  border: 1px solid #ccc;
  padding: 10px 12px;
  text-align: center;
}
.data-table th {
  background-color: #f7f7f7;
  color: #003049;
  font-weight: bold;
}
</style>
