<template>
  <div class="table-section">
    <h3 class="section-title" v-if="title">{{ title }}</h3>
    <div class="table-wrapper">
      <table class="data-table">
        <thead>
          <tr>
            <th>{{ rowLabel }}</th>
            <th v-if="hasDefinition">含义</th>
            <th v-for="col in columns" :key="col">{{ col }}</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(row, rowIndex) in rowDataWithTotal" :key="rowIndex">
            <td>{{ row.label }}</td>
            <td v-if="hasDefinition">{{ row.definition || '-' }}</td>
            <td v-for="col in columns" :key="col" style="text-align: right;">
              {{ formatNumber(row[col]) }}
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TransposedTable',
  props: {
    data: { type: Array, default: () => [] },
    rowKey: { type: String, required: true },
    colKey: { type: String, required: true },
    valKey: { type: String, required: true },
    title: { type: String, default: '' },
    rowLabel: { type: String, default: '名称' },
    transpose: { type: Boolean, default: true }
  },
  computed: {
    hasDefinition() {
      return this.data.some(row => row.definition);
    },
    columns() {
      const set = new Set(this.data.map(item => item[this.colKey]));
      return Array.from(set).sort();
    },
    rowData() {
      const grouped = {};
      this.data.forEach(item => {
        const rowName = item[this.rowKey];
        const colName = item[this.colKey];
        const val = parseFloat(item[this.valKey]);

        if (!grouped[rowName]) {
          grouped[rowName] = {
            label: rowName,
            definition: item.definition || ''
          };
        }

        grouped[rowName][colName] = (grouped[rowName][colName] || 0) + (isNaN(val) ? 0 : val);
      });

      // ❗不要再额外排序，否则会破坏后端传来的顺序
      return Object.values(grouped);
    },
    rowDataWithTotal() {
      const rows = this.rowData;

      // 如果已有“合计”行，直接返回
      if (rows.some(row => row.label === '合计')) {
        return rows;
      }

      // 动态构建合计行
      const totalRow = { label: '合计', definition: '' };
      this.columns.forEach(col => {
        totalRow[col] = rows.reduce((sum, row) => {
          const val = parseFloat(row[col]);
          return sum + (isNaN(val) ? 0 : val);
        }, 0);
      });

      return [...rows, totalRow];
    }
  },
  methods: {
    formatNumber(val) {
      const num = parseFloat(val);
      return isNaN(num) ? '-' : num.toLocaleString(undefined, { minimumFractionDigits: 2 });
    }
  }
};
</script>

<style scoped>
.table-section {
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
  width: 100%;
  overflow-x: auto;
}
.section-title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 16px;
  color: #003049;
}
.data-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 14px;
}
.data-table th,
.data-table td {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: center;
}
.data-table th {
  background-color: #f7f7f7;
  color: #003049;
  font-weight: bold;
}
</style>
